import Pagination from '~/components/Pagination'
import SiteNotice from '~/components/SiteNotice'

export default {
  components: {
    Pagination,
    SiteNotice
  },
  async asyncData({ $axios, params }) {
    const [tagsPage] = await Promise.all([
      $axios.get('/api/tag/tags', {
        params: {
          page: params.page
        }
      })
    ])
    return {
      tagsPage
    }
  },
  head() {
    return {
      title: this.$siteTitle('标签')
    }
  }
}
